import React from 'react';
import { useApp } from '../../contexts/AppContext';

function Header() {
  const { state, dispatch } = useApp();

  const toggleFacilitatorMode = () => {
    dispatch({ type: 'TOGGLE_FACILITATOR_MODE' });
  };

  return (
    <header className="header">
      <div className="container">
        <div className="header-content">
          <div className="logo-section">
            <div className="logo-placeholder">
              <img 
                src={process.env.PUBLIC_URL + '/logo192.png'}
                alt="Logo Seminario" 
                className="logo-text"
                style={{ 
                  width: '40px', 
                  height: '40px',
                  borderRadius: '50%',
                  objectFit: 'cover',
                  background: 'white'
                }}
                onError={(e) => {
                  e.target.style.display = 'none';
                  e.target.nextSibling.style.display = 'flex';
                }}
              />
              <span 
                className="logo-text" 
                style={{ display: 'none' }}
              >
                IA
              </span>
            </div>
            <h1 className="header-title">A Confronto con l'Intelligenza Artificiale</h1>
            <p className="header-subtitle">Strategie Avanzate per Amministratori di Condominio</p>
          </div>
          
          {(!state.sessionActive || state.facilitatorMode) && (
            <button 
              onClick={toggleFacilitatorMode}
              className={`facilitator-toggle ${state.facilitatorMode ? 'active' : ''}`}
            >
              {state.facilitatorMode ? ' Modalit Partecipante' : ' Modalit Facilitatore'}
            </button>
          )}
        </div>
      </div>
    </header>
  );
}

export default Header;