      title: 'Framework RACE per Prompt Efficaci',
      description: 'Metodologia strutturata per costruire prompt professionali che ottengono risultati precisi e coerenti.',
      content: `# Framework RACE per Prompt Efficaci

## Cos' il Framework RACE?

Il Framework RACE  una metodologia strutturata per costruire prompt che ottengono risultati professionali dall'IA. Ogni lettera rappresenta un elemento essenziale:

**R - RUOLO**: Chi deve essere l'IA per te
**A - AZIONE**: Cosa deve fare esattamente  
**C - CONTESTO**: La situazione specifica
**E - ESEMPIO**: Come deve essere l'output

## Come Applicarlo

### R - RUOLO
Definisci chi deve "essere" l'IA:
- "Sei un amministratore condominiale esperto"
- "Agisci come un consulente legale specializzato"
- "Comportati come un mediatore professionale"

### A - AZIONE  
Specifica cosa deve fare:
- "Redigi una comunicazione formale"
- "Analizza questo contratto"
- "Riassumi i punti chiave"

### C - CONTESTO
Fornisci la situazione completa:
- Tipo di condominio
- Problema specifico
- Stakeholder coinvolti
- Vincoli temporali

### E - ESEMPIO
Indica formato e stile desiderati:
- "Tono professionale ma empatico"
- "Formato email con oggetto chiaro"
- "Massimo 200 parole"

## Esempio Pratico

**PROMPT SENZA RACE:**
"Scrivi una lettera per rumori molesti"

**PROMPT CON RACE:**
**R**: Sei un amministratore condominiale esperto in gestione diplomatica
**A**: Redigi comunicazione formale per disturbi acustici recidivi
**C**: Condominio 14 unit, terza segnalazione stesso appartamento, rumori serali 22-24, necessit escalation ma preservando clima
**E**: Tono autorevole ma diplomatico, formato lettera ufficiale, 250-300 parole, include riferimenti normativi

## Risultati Attesi

Con RACE otterrai:
-  Output pi precisi e professionali
-  Riduzione tempo di revisione
-  Maggiore coerenza nei risultati
-  Comunicazioni pi efficaci`
